import { commumal } from "../public";

var listWindow={
     content(){
          let html=`
          <div class='drawer'>
            <input type='text' class='input' placeholder="姓名、单号">
          <p class='headline'>日期:</p>
            <ul class='date'>
              <li class='seleted' data-index='0'>今天</li>
              <li data-index='1'>一周</li>
              <li data-index="2">三周</li>
              <li data-index="3">当月</li>
              <li data-index="4">三月</li>
              <li data-index="5">半年</li>
              <li data-index="6">一年</li>
           </ul>
        </div>
          `
         return html;
     },
     query(opts){
         let html=listWindow.content();
         let $html=$(html)
        $.showbox({
            content:$html,
            cls:"select",
            buttons: [{
                text: '查询',
                click:function () {
                    let data=listWindow.getData();
                     if(typeof opts.loadCondition=='function'){
                          opts.loadCondition(data)
                     }
                     if(typeof opts.fresh=='function'){
                         opts.fresh(data)
                     }
                     localStorage.setItem(opts.cache,JSON.stringify(data));
                     $.hidebox()
                }
            }],
            beforeShow:function(){
                 listWindow.fillTime();
                 let arr=JSON.parse(commumal.caching(opts.cache));
                 if(arr){
                      listWindow.handleCondition(arr)  //处理缓存
                 }
                 $(".select").find(".date li").on("click",listWindow.liClick)
            }
         })
     },
     fillTime(){
        var li=$(".select").find(".date li");
        var date=new Date();
        var month=date.getMonth();
        var year=date.getFullYear();
        var oneDay=date.getDate();
        var day=date.getDay();
        if(month==11){
            month=-1;
            year+=1;
        }
        $.each(li,(i,v)=>{
            let index=$(v).attr("data-index");
            if(index==0){
              $(v).attr('data-start',moment().format('YYYY-MM-DD'));
              $(v).attr('data-end',moment().format('YYYY-MM-DD'));
            }else if(index==1){
                if(day!=0){
                  $(v).attr("data-start",moment().subtract(day-1, 'd').format('YYYY-MM-DD'));
                  $(v).attr("data-end",moment().subtract(day-7, 'd').format('YYYY-MM-DD'));
                }else{
                  $(v).attr("data-start",moment().subtract(6, 'd').format('YYYY-MM-DD'));
                  $(v).attr("data-end",moment().format('YYYY-MM-DD'))
                }
            }else if(index==2){
              if(day!=0){
                  $(v).attr("data-start",moment().subtract(13+day, 'd').format('YYYY-MM-DD'))
                  $(v).attr("data-end",moment().subtract(day-7, 'd').format('YYYY-MM-DD'))
                 }else{
                   $(v).attr("data-start",moment().subtract(21, 'd').format('YYYY-MM-DD'))
                   $(v).attr("data-end",moment().format('YYYY-MM-DD'))
                 }
            }else if(index==3){
              $(v).attr("data-start",moment().subtract(oneDay-1, 'd').format('YYYY-MM-DD'))
              $(v).attr("data-end",moment([year,month+1,1]).subtract(1,"d").format('YYYY-MM-DD'))
            }else if(index==4){
              $(v).attr("data-start",moment().subtract(oneDay-1, 'd').subtract(2,"M").format('YYYY-MM-DD'))
              $(v).attr("data-end",moment([year,month+1,1]).subtract(1,"d").format('YYYY-MM-DD'))
            }else if(index==5){
              $(v).attr("data-start",moment().subtract(oneDay-1, 'd').subtract(5,"M").format('YYYY-MM-DD'))
              $(v).attr("data-end",moment([year,month+1,1]).subtract(1,"d").format('YYYY-MM-DD'))
            }else if(index==6){
              $(v).attr("data-start",moment().subtract(oneDay-1, 'd').subtract(11,"M").format('YYYY-MM-DD'))
              $(v).attr("data-end",moment([year,month+1,1]).subtract(1,"d").format('YYYY-MM-DD'))
            }
        })
     },
     handleCondition(data){
        for(let i=0;i<data.length;i++){
            let $li=data[i];
            if($li.value.index==0){
               $(".select .input").val($li.name);
            }else{
               $(".select .date li[data-start="+$li.value.value.start+'][data-end='+$li.value.value.end+']').addClass("seleted").siblings().removeClass("seleted")
            }
        }
     },
     liClick(){
         $(this).addClass("seleted").siblings().removeClass("seleted")
     },
     getData(){
        let input=$('.select').find('.input').val();
        let time=$(".select").find('.date .seleted');
        let selectData
        if(input){
           selectData=[
               {name:input,value:{index:0,value:input}},
               {name:time.text(),value:{index:1,value:{start:time.attr('data-start'),end:time.attr('data-end')}}}
           ]
        }else{
           selectData=[
               {name:time.text(),value:{index:1,value:{start:time.attr('data-start'),end:time.attr('data-end')}}}
            ]
        }
        
        return selectData
     },
     defaultDelete(){
         //没有缓存时默认的今天的按钮的删除
         let $li=$(this).closest("li");
         $li.remove()
     },
     backToStartPoint(){
         //查询时重新回到顶部
        $(".content").scrollTop(0);
        $(".getMore").addClass("dn")
     },
}

export default listWindow


