var versionUpdate={
     events:{
        str:(param)=>{
            let html=`
            <div class="weui-article">
            <h1>版本更新介绍</h1>
            <div class="boxa">
            <section>
               <h3 class="blue">当前版本：v${param}<a class="ml10 shouce">下载操作手册</a></h3>
                <h4>一、新增待审核模块</h4>
                <p>1、将之前的报销管理、外勤管理、员工请假以及加班申请模块里的待审核部分整合出来作为一个单独的模块。</p>
                <h4>二、优化页面布局</h4>
                <p>1、在可视区域宽度大于768px的设备中，网页都以375px的宽度居中显示</p>
                <p>2、之前版本各个模板都在一个单页面中，当前版本将每个模块独立为一个单页面</p>
                <p>3、更换了模块内按条件搜索的布局</p>
                <h4>三.页面图标的更换</h4>
            </section>
  
            </div>
           </div>
            `
          return html;
        },
        version:(param)=>{
            let html=versionUpdate.events.str(versionUpdate.config.versionNumber);
            $.showbox({
                content:html,
                cls: 'tyi',
                buttons: [{
                    text: '关闭',
                    click: function() {
                       if(typeof param=='function'){
                           param()
                       }
                       $.hidebox()

                    }
                }],
                beforeShow: function() {
                    $(".popup_content").addClass("ver")
                    $(".tyi").find("a.shouce").on("click",function () {
                        location="../static/document/OA_Manual.pdf"

                    })

                },

            });
        }
     },
     config:{
        versionNumber:3.1
     }
}
export default versionUpdate


