import {commumal,listWindow,descroll} from "../public"
var askforLeaveList={
    methods:{
        init(){
            $(".weui-btn.query").on("click",askforLeaveList.events.openWindow);
            $(".content").scroll(askforLeaveList.events.scroll);
            $(".condition ul li i").on('click',listWindow.defaultDelete)
        }
    },
    events:{
         openWindow(){
            listWindow.backToStartPoint()
            listWindow.query({
                cache:"DATE",
                loadCondition:(data)=>{
                    askforLeaveList.events.loadCondition(data)
                },
                fresh:(data)=>{
                    askforLeaveList.events.fetchData(data,{refresh:true,complete:false})
                }
            })
         },
         loadCondition(data){
            let html="";
            for(let i in data){
               html+=`
                 <li value="${data[i].value.index}">
                     <span>${data[i].name}</span>
                     <i class="iconfont icon-x"></i>
                 </li>
               `
            }
           let $html=$(html);
            $html.find("i").on("click",askforLeaveList.events.deleteli)
            $(".condition ul").html($html)
         },
         deleteli(){
            listWindow.backToStartPoint()
            let $li=$(this).closest("li");
            let index=$li.attr("value");
            let arr=JSON.parse(commumal.caching("DATE"));
            for(let i=0;i<arr.length;i++){
                if(arr[i].value.index==index){
                    arr.splice(i,1)
                }
            }
            askforLeaveList.events.fetchData(arr,{refresh:true,complete:false})
            localStorage.setItem("DATE",JSON.stringify(arr))
            $li.remove()
         },
         fetchData(data,ref){
            let submitData={}
            for(let i in data){
                if(data[i].value.index==0){
                   submitData.QUERY=data[i].value.value   //取input框里面的内容
                }else{
                    let time=data[i].value.value;
                    submitData.DATE_FRM=time.start;
                    submitData.DATE_TO=time.end;   //取开始结束时间
                }
            }
            if(ref.refresh){
                descroll.config.page.index=1;
              }
            let date=moment().format('YYYY-MM-DD');
          
            let endData={
                m:"LIST",
                SP_NAME:"SP_LVLV_LEAVE_INFO_LIST",
                SELF_IND:"Y",
                QUERY:submitData.QUERY||"",
                DATE_FRM:submitData.DATE_FRM||date,
                DATE_TO:submitData.DATE_TO||date,
                page:descroll.config.page.index++,
                rows:descroll.config.page.rows
            }
            
            askforLeaveList.events.fresh(endData,ref)
         },
        fetchCache(ref){ //判断是否有缓存
            let arr=JSON.parse(commumal.caching("DATE"));
            let time=moment().format('YYYY-MM-DD');
            if(!arr){
                let value=[{name:"今天",value:{index:1,value:{start:time,end:time}}}]
                askforLeaveList.events.fetchData(value,ref);
                $(".condition ul li").attr("value",1)
            }else{
                askforLeaveList.events.loadCondition(arr);
                askforLeaveList.events.fetchData(arr,ref)
            }
        },
        fresh(subData,ref){
            $.defs.loadingshow();
            $.defs.myPost(subData).then((res)=>{
                $.defs.loadinghide();
                for(let i=0;i<res.rows.length;i++){
                    res.rows[i].ApplyDate=commumal.dateToday(res.rows[i].LVLV_ENTRY_DT)
                    res.rows[i].toDay=Math.floor(res.rows[i].LVLV_HOUR/4)/2;
                }
             
                let html=askforLeaveList.events.content(res.rows);
                let $html=$(html);
                $html.find(".delete").on("click",askforLeaveList.events.delete);
                $html.on("click",askforLeaveList.events.matterClick);
                descroll.config.pages(res);
                if(ref.complete){
                    if(res.rows.length!=0){
                         $(".content").append($html);
                          descroll.config.isLoad=true;
                    }else{
                        $(".content").empty()
                    }
                }else{
                    if(res.rows.length!=0){
                        $(".content").html($html)
                    }else{
                        $(".content").empty()
                    }
                }
                 
            })
        },
        content(data){
            var  html='';
            for(let i=0;i<data.length;i++){
                 html+=`
                    <div class="weui-form-preview item" index="${data[i].LVLV_KY}" lvlvId="${data[i].LVLV_ID}" type="${data[i].SYSV_LVLV_TYPE_DESC}" sts="${data[i].LVLV_STS}">
                     <div class="weui-form-preview__hd">
                       <div class="weui-form-preview__item">
                          <label class="weui-form-preview__label black f12">${data[i].LVLV_ID}</label>
                           <em class="weui-form-preview__value">${data[i].LVLV_STS_DESC}</em>
                       </div>
                      </div>
                      <div class="weui-form-preview__bd f12">
                        <div class="weui-form-preview__item">
                            <label class="weui-form-preview__label black">${data[i].MEME_NAME}</label>
                            <span class="weui-form-preview__value black">休假时长:${data[i].LVLV_HOUR}小时(共${data[i].toDay}天)</span>
                        </div>
                        <div class="weui-form-preview__item">
                            <label class="weui-form-preview__label black">休假时间</label>
                            <span class="weui-form-preview__value black f12">${data[i].LVLV_FRM_DTM}至${data[i].LVLV_TO_DTM}</span>
                         </div>
                         <div class="weui-form-preview__item">
                           <label class="weui-form-preview__label black">${data[i].SYSV_LVLV_TYPE_DESC}</label>
                           <span class="weui-form-preview__value black f12">申请时间:${data[i].ApplyDate}</span>
                        </div>
                       </div>
                       ${askforLeaveList.events.operationBtn(data[i])}
                    </div>
                 `
            }
            return html;
        },
        operationBtn(data){
            let html="";
             if(data["LVLV_STS"]<331){
                 html=`
                 <div class="weui-form-preview__ft">
                     <a class="weui-form-preview__btn weui-form-preview__btn_primary delete" href="javascript:">删除</a>
                   </div>
                 `
             }
             return html
        },
        delete(ev){
            ev.stopPropagation();
            ev.preventDefault();
            var $item=$(this).closest(".weui-form-preview");
            var lvlvKey=$item.attr("index");
            var lvlvId=$item.attr("lvlvId");
            var type=$item.attr("type");
            commumal.confirm({
                 title:'确定删除单号为'+lvlvId+"的"+type+"申请？",
                 click:()=>{
                     askforLeaveList.events.deleteItem(lvlvKey)
                 }
            })
        },
        deleteItem(key){
              $.defs.loadingshow("正在删除");
              $.defs.myPost({
                m:"delete",
                SP_NAME:"SP_LVLV_LEAVE_INFO_STATUS_UPDATE",
                REVIEW_IND:"D",
                LVLV_KY:key
              }).then((data)=>{
                  $.defs.loadinghide();
                  if(data.RETURN_CODE==0){
                      askforLeaveList.events.fetchCache({refresh:true,complete:false});
                      weui.toast('删除成功', 3000);
                  }else{
                     $.defs.msg(data.RETURN_MESSAGE)
                  }
              })
        },
        matterClick(){
            var $item=$(this).closest(".weui-form-preview");
            var ivivKey=$item.attr("index");
             var sts=$item.attr("sts");
             if(sts<331){
                 //修改
                spspa.setMessage("editId",{
                    'content': {
                        ivivKey:ivivKey
                    }
                });
                 window.spspa.go("/add")
             }else{
                 //查看
                 spspa.setMessage("queryID",{
                    'content': {
                        lvlvKy:ivivKey,
                        sts:sts,
                    }
                })
                spspa.go("/myQuery")
             }
        },
        scroll(){
            var that=this;
            descroll.events.onscroll(that,{
                datafresh:function () {
                       askforLeaveList.events.fetchCache({refresh:false,complete:true})
                }
            })
        }
    },
    config:{
        init(){
            $(".top .huan").text("请假流程>我的");
             askforLeaveList.events.fetchCache({refresh:true,complete:false})  //打开或手动刷新页面取缓存

        }
    }
}
$(function(){
    askforLeaveList.config.init();
    askforLeaveList.methods.init();
})


