var addational={
       list:null,
       content(){
           let html=`
           <div class="weui-cell">
              <div class="weui-cell__bd">
               <input class="weui-input"  placeholder="请输入工号或姓名"/>
             </div>
              <div class="weui-cell__ft">
                <span class="weui-vcode-btn query">查询</span>
              </div>
            </div>
            <div class="weui-cells receip f12">

            </div>
           `
           let $html=$(html);
           return $html;
       },
       bills(opts){
         let $Shtml=addational.content();
         $.showbox({
            content:$Shtml,
             cls:"Additionreceipts",
             buttons: [{
                text: '关闭',
                click() {
                    $.hidebox()
                }
             }],
             beforeShow(){
                 addational.query(opts);
             }
         })

       },
       query(param){
         $.defs.loadingshow()
         $.defs.myPost({
            SP_NAME: 'SP_ATDC_ATTENDANCE_INFO_FEPP_LIST',
            m: 'SELECT',
            QUERY: $(".Additionreceipts").find('.weui-input').val(),
            APPLY_EHUSER: $('.applyUser').attr('data-value'),
            DATE: $('.SelectedDate').find('span').text()
         }).then((ref)=>{
             $.defs.loadinghide();
             addational.list=ref;
             $(".Additionreceipts").find(".query").on("click",()=>{
                 addational.query(param)
           })
           let html=addational.billList(ref);
           let $html=$(html);
            $(".Additionreceipts").find(".receip").html($html);
            $(".Additionreceipts").find(".add_rep").on("click",function(){
                let self=this;
                if(typeof param.span=='function'){
                     param.span(self)
                }
            })
         })
       },
       billList(data){
           let html='';
           for(let i=0;i<data.length;i++){
               html+=`
                <div class="weui-cell add_rep" ATDC_KY="${data[i].ATDC_KY}" style="border-bottom: 1px solid #ccc">
                 <div class="weui-cell__bd">
                    <p class="number"><span class="cardNumber">${data[i].ATDC_ID}</span><span class="fr cardType">${data[i].SYSV_ATDC_DESC}</span></p>
                    <p>${data[i].ATDC_START_DT}起${data[i].ATDC_DAY}天${data[i].ATDC_HUMAN_CNT}人</p>
                    <p>地区范围:${data[i].BSTR_NAME}</p>
                    <p>详细地址:${data[i].ATDC_SITE}</p>
                 </div>
                </div>
               `
           }
           return html

       }
}
export default addational


