var descroll={
       events:{
           onscroll:function (that,opts) {
               if(that.scrollHeight - that.offsetHeight - that.scrollTop <100){
                   if(descroll.config.page.index <= descroll.config.total){
                       
                       if(descroll.config.isLoad){
                          descroll.config.isLoad=false;
                           opts.datafresh()
                       }
                   }else if(descroll.config.page.index > descroll.config.total){
                       if(descroll.config.isLoad){
                           $(".page__ft:last").removeClass("dn")

                       }
                   }
               }
               if (that.scrollHeight - that.offsetHeight - that.scrollTop > 100){
                    $(".page__ft").addClass("dn")
               }
           }
       },
       config:{
           page:{
               index:1,
               rows:10
           },
           isLoad: true,
           total: 1,
           pages:function (data) {
               descroll.config.total=Math.ceil(parseFloat(data.total / descroll.config.page.rows)); //判断有多少页
              
           },
       }
}
export  default descroll


