var applyer={
      content(){
           let html=`
           <div class="weui-cell">
             <div class="weui-cell__bd">
                <input class="weui-input"  placeholder="请输入工号或姓名"/>
             </div>
             <div class="weui-cell__ft">
                <span class="weui-vcode-btn query">查询</span>
              </div>
          </div>
          <div class="member" style="padding:10px">
            
          </div>
           `
           let $html=$(html);
           return $html
      },
     employee(opts){
         let self=this;
         let $Shtml=applyer.content();
        $.showbox({
            content:$Shtml,
            cls:"staff",
            buttons: [{
                text: '关闭',
                click() {
                    $.hidebox()
                }
            }],
            beforeShow() {
                  self.query(opts)
            }
        })
     },
     query(param){
        var ainput=$(".staff").find(".weui-input").val();
        $.defs.loadingshow();
        $.defs.myPost({
            m:"LIST",
            SP_NAME:"SP_DPPS_APPLY_LIST",
            MEME_QUERY:ainput
        }).then((res)=>{
            $.defs.loadinghide()
            $(".staff").find(".query").on("click",()=>{
                 applyer.query(param)
            })
            let data=res.rows;
           let html = applyer.staffList(data);
           let  $html=$(html);
            $(".staff").find(".member").html($html);
            $(".staff").find(".membersName").on("click",function(){
                  let self=this;
                 if(typeof param.span=='function'){
                     param.span(self)
                 }
            })


        })
     },
     staffList(data){
         let html='';
         for(let i=0;i<data.length;i++){
             html+=`
              <span class="membersName" apps="${data[i].DPPS_KY}">${data[i].MEME_NAME}(${data[i].DPDP_NAME})</span>
             `  
         }
         return html

     }
}
export default applyer


